.chat {
    flex: 0.7;
    flex-grow: 1;
    overflow-y: scroll;
    height: 50vh;
}

.chat_header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    border-bottom: 1px solid lightgray;
}

.chat_channelName {
    display: flex;
    align-items: center;
    text-transform: lowercase;
}

.chat_channelName > .MuiSvgIcon-root {
    margin-left: 10px;
    font-size: 18px;
}

.chat_headerRight > p {
    display: flex;
    align-items: center;
    font-size: 14x;
}

.chat_headerRight > p > .MuiSvgIcon-root {
    margin-right: 5px !important;
    font-size: 16px;
}
.content-right{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}

@media (min-width: 768px) {
    .chat {
        padding-bottom: 150px;
        width: 100%;
    }
}
@media (max-width: 768px) {
    .chat {
        margin-bottom: -100px;
    }
}